@import "../../../../assets/styles/foundations.scss";

.container {
	position: fixed;
	top: 0;
	left: 0;

	height: 100vh;
	width: 100vw;

	display: grid;
	grid-template-rows:
		minmax($px53, $px64)
		minmax(32.4375rem, 75.75rem) //new
		minmax($px53, $px64);

	background-color: rgba(50, 50, 50, 0.65);
	backdrop-filter: blur(4px);
	z-index: 50;

	&__wrapperRelative {
		position: relative;

		&__closeBtn {
			height: $px48;
			width: $px48;

			position: absolute;
			right: $px48;
			bottom: 0;

			border-radius: 0.4rem;
			background: rgba(122, 122, 122, 0.4);
			z-index: 100;

			&:hover {
				background-color: $neutral-5;
			}
		}
	}

	&__leftArrow,
	&__rightArrow {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 100;
	}

	&__leftArrow {
		left: $px48;
	}

	&__rightArrow {
		right: $px48;
	}

	&__imgContainer {
		grid-area: 2/1/3/2;
		display: flex;
		width: 100%;

		& > :first-child {
			margin: auto;
			box-shadow: 0px 0px 25px 7px rgba(50, 50, 50, 0.15);
			max-height: 100%;

			:first-child {
				max-height: 100%;
			}
		}
	}

	&__zoomWrapper {
		position: relative;
		display: flex;
		height: 5rem; //new

		&__zoomContainer {
			position: absolute;
			display: flex;
			top: 0;
			left: calc(100vw - 50%);
			transform: translateX(-50%);
			background-color: $neutral-4;
			border-radius: $radius;

			& > :first-child {
				border-radius: $px4 0 0 $px4;
			}

			& > :last-child {
				border-radius: 0 $px4 $px4 0;
			}
		}
	}
}
