@import "../../../../assets/styles/foundations.scss";

.header {
	display: flex;
	justify-content: space-between;
	padding: $px32 $px64 0 $px32;
	margin-bottom: $px34;
}

.centredWrapper {
	display: flex;
	justify-content: center;

	&__container {
		display: flex;
		flex-direction: column;
		width: 50.75rem; //new

		& > :last-child {
			align-self: flex-end;
		}

		h2 {
			padding-left: $px40;
			margin-bottom: $px36;
			font-weight: bold;
			font-size: 42px;
			line-height: 50px;
			letter-spacing: 0.4px;
			color: $neutral-6;
		}

		&__body {
			margin-bottom: $px36;

			& > :not(:last-child) {
				margin-bottom: $px27;
			}
		}
	}
}
