@import "../../../../../../../../assets/styles/foundations.scss";

.active,
.default {
  height: $px10;
  width: $px10;
  border-radius: 50%;
  box-shadow: 0px 0px 20px 8px rgba(255, 255, 255, 0.25);

  svg {
    max-height: 100%;
  }

  i {
    height: inherit;
    width: inherit;
  }
}

.active {
  circle {
    fill: $neutral-4;
  }
}

.default {
  circle {
    fill: $neutral-1;
  }
}
