@import "../../../../../assets/styles/foundations.scss";

.contentWrapper {
  width: auto;
  padding: $px32 $px64;
  overflow-y: auto;
  box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, 0.1);

  &__body {
    margin: $px48 0;

    // textarea {
    // 	transition: all 2s;
    // }

    .container {
      margin-bottom: $px40;
      color: $neutral-6;

      & :first-child {
        margin-bottom: $px24;
      }

      h2 {
        font-weight: bold;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: 0.4px;
      }

      p {
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
      }
    }

    &__answersField {
      & > :not(:last-child) {
        margin-bottom: $px36;
      }
    }
  }
}
